.App {
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
}

.globalnavigation {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 15px 0;
}

.fodnote {
  display: flex;
  justify-content: space-around;
}

/*-------- kort formattering ---------*/

.kortraekke {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.kort{
  position: relative;
  width: 240px;
  margin: 10px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.3);
}

.kort .tekst{
  text-align: center;
  padding: 0 10px 50px;
}

.kort .tekst p:nth-of-type(1){
  min-height: 65px;
}

.kort h3{
  text-align: center;
  margin-bottom: 5px;
}
